import React, {useState} from "react";
import {getStorage, ref, uploadBytes, getDownloadURL} from "firebase/storage";
import {getAuth, updateProfile} from "firebase/auth";
import imageCompression from "browser-image-compression";

const UploadProfilePicture = () => {
    const [file, setFile] = useState(null);
    const [previewUrl, setPreviewUrl] = useState(null);
    const [uploading, setUploading] = useState(false);
    const [success, setSuccess] = useState("");
    const [error, setError] = useState("");
    const auth = getAuth();
    const user = auth.currentUser;
    const handleFileChange = async (e) => {
        const selected = e.target.files[0];
        if (selected && selected.type.startsWith("image/")) {
            try {
                // Opzioni di compressione
                const options = {
                    maxSizeMB: 0.2,            // max 200KB
                    maxWidthOrHeight: 400,     // max dimensione
                    useWebWorker: true,
                };
                const compressedFile = await imageCompression(selected, options);
                setFile(compressedFile);
                // Crea un'anteprima
                const preview = await imageCompression.getDataUrlFromFile(compressedFile);
                setPreviewUrl(preview);
                setError("");
            } catch (err) {
                console.error("Compression error:", err);
                setError("Errore nella compressione immagine.");
            }
        } else {
            setError("Seleziona un file immagine valido.");
        }
    };
    const handleUpload = async () => {
        if (!file || !user) return;
        const storage = getStorage();
        const storageRef = ref(storage, `profilePictures/${user.uid}.jpg`);
        setUploading(true);
        setSuccess("");
        setError("");
        try {
            await uploadBytes(storageRef, file);
            const downloadURL = await getDownloadURL(storageRef);
            await updateProfile(user, {photoURL: downloadURL});
            setSuccess("Foto caricata e profilo aggiornato!");
        } catch (err) {
            console.error(err);
            setError("Errore durante il caricamento.");
        } finally {
            setUploading(false);
        }
    };
    return (
        <div>
            <h3>Carica la tua foto profilo</h3>
            <input type="file" accept="image/*" onChange={handleFileChange}/>
            {previewUrl && <img src={previewUrl} alt="Anteprima" width={150}/>}
            <br/>
            <button onClick={handleUpload} disabled={!file || uploading}>
                {uploading ? "Caricamento..." : "Carica"
                }
            </button>
            {success && <p style={{color: "green"}}>{success}</p>}
            {error && <p style={{color: "red"}}>{error}</p>}
        </div>
    );
};
export default UploadProfilePicture;